.footer{min-height:100px;display:flex;flex-direction:raw;justify-content:center;align-items:stretch;flex-wrap:nowrap;background-color:#111}.footer .container{width:80%;margin:0 auto;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;flex-wrap:nowrap}.footer .container .title{font-family:"Josefin Sans",sans-serif;font-size:25px;line-height:1.2;font-weight:400;color:#fff;align-items:center}.footer .container .socialIcons{display:flex;justify-content:space-between;box-sizing:border-box;padding:0;margin:0}.footer .container .socialIcons .socialIcon{margin:0 2px;font-size:20px;text-align:center;transition:all .5s;cursor:pointer;width:40px;height:40px;line-height:40px;border-radius:50%;border:1px solid #222}.footer .container .socialIcons .socialIcon .gitHub::before{content:""}.footer .container .socialIcons .socialIcon .codewars::before{content:""}.footer .container .socialIcons .socialIcon .linkedin::before{content:""}.footer .container .socialIcons .socialIcon .telegram::before{content:""}.footer .container span{font-family:"Open Sans",sans-serif;font-size:10px;line-height:1.2;font-weight:400;color:#fff;margin-bottom:14px}